@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Larsseit";
  color: #333;
}

body {
  background: #edf0f5;
  opacity: 0.8;
  background-size: 10px 10px;
}

.navbar {
  padding: 10px;
  display: flex;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #ffffff;
  background: linear-gradient(90deg, #000000, #003f47) !important;
  justify-content: space-between;
}

.navbar h1 {
  color:  #ffffff;
  font-family: "Larsseit";
  font-size: 45px;
  margin-bottom: 8px;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: #2a808b
}

.content {
  max-width: 700px;
  margin: 40px auto;
  padding: 20px;
}
/* hompage css / list */

.homepage {
  box-shadow: 0 1px 3px 0 #000000;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  text-align: center;
  display: grid;
  padding: 10px 16px;
  animation: 1.25s flyin ease;
  margin: 2px 0;
  border-bottom: 1px solid #fafafa;
}

/* Animation property */
.homepage button {
  animation: wiggle 6s linear infinite;
  
}

.homepage div {
  padding: 6%;
  contain: content;
  flex-wrap: nowrap;
}

.homepage:hover {
  box-shadow:  0 1px 3px 0  #000000;
}

.homepage h1 {
  color:  #414141;

  font-family: "Larsseit"; 
}

.homepage p {
  font-size: 15px;
  color:  #ffffff;
  font-weight: bold;
  margin-bottom: 1px;
  font-family: "Larsseit";
}

.homepage a{
  text-decoration: none;
}

.homepage input, .homepage textarea, .homepage select  {
  background-color: #ffffff !important;
  font-weight: bold;
  font-size: 16px;
  color: rgb(0, 0, 0);
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  font-family: "Larsseit";
  box-sizing: border-box;
  display: block;
  border-radius: 4px;
}

.homepage button {
  background: linear-gradient(90deg, #000000, #003f47) !important;
  color: rgb(3, 0, 0);
  font-family: "Larsseit";
  border: 0;
  padding: 8px;
  display: inline;
  border-radius: 8px;
  cursor: pointer;
}

.homepage strong{
  font-size: large;
  line-height: 17px;
  display: block;
  color: #ffffff;
  color:  #003f47;
  font-family: "Larsseit";
}
/* form previews / list */
.form-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #aad6db;
  animation: 1.25s flyin ease;
}

.form-preview:hover {
  box-shadow:  0 4px 8px 0  #000000;
}

.form-preview h2 {
  font-size: 19px;
  color:  #003f47;
  margin-bottom: 8px;
  font-family: "Larsseit";
}

.form-preview p {
  font-size: 15px;
  color:  #6b240e;
  font-weight: bold;
  margin-bottom: 1px;
  font-family: "Larsseit";
}

.form-preview a{
  text-decoration: none;
}
/* form details page */
.form-details {
  box-shadow: 0 4px 8px 0 #003f47;
  transition: 0.3s;
  width: 100%;
  margin: 10px;
  margin-inline-end: 50px;
  border-radius: 5px;
  background-color: white;
  animation: 1.25s flyin ease;
}

.form-details:hover {
  box-shadow:  0 4px 8px 0 #003f47;
}

.form-details h2 {
  font-size: 20px;
  text-align: center;
  color: #4b2faf;
  margin-bottom: 10px;
}

.form-details p {
  font-size: 15px;
  color: #000000;
  margin-bottom: 1px;
}

.form-details div {
  margin: 20px 0;
  font-size: 15px;
  color: #000000;
}

.form-details button {
  background: linear-gradient(90deg, #000000, #003f47) !important;
  font-weight: bold;
  color: rgb(255, 255, 255);
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.form-details table {
  color:  #003f47;
  font-family: "Larsseit";
  border-collapse: collapse;
  width: 100%;
}

.form-details div {
  color: rgba(0, 0, 0, 0.747);
}

.form-details  td, .create  th {
  border: 1px solid rgba(0, 0, 0, 0.836);
  padding: 8px;
  text-align: center;
}

.form-details tr:nth-child(even){background-color: rgb(228, 228, 228);}

.form-details  tr:hover {background-color: rgb(194, 190, 190);}

.form-details  th {
  padding-top: 12px;
  padding-bottom: 12px;
  color: #ffffff;
  text-align: center;
  background-color: #003f47;
  border: 1px solid rgb(0, 0, 0);
  color:  #eef6f7;
  font-family: "Larsseit";
  ;
}
.form-details strong{
  text-align: center;
}

.form-details div{
  color: #000000;
  text-align: center;
}

